<template>
  <div class="container" :class="[showDis ? 'fixedView':'']">
			
			<!--轮播图-->
			<div class="bannerView">
				<swiper class='banner' @change='bannerChange'>
				    <swiper-item v-for='(item,key) in bannerList' :key='item.id'>
				      <img src="../../../static/img/list1.png" class="slide-image"/>
				    </swiper-item>
				</swiper>
				<div class="pageTion">
		    	<span v-for='(item,key) in bannerList' :key='item.id' :class="[bSel == key ? 'sel':'']"></span>
		    </div>
			</div>
			
			<!--标题-->
			<div class="proTil">
				<div class="til">粉黛自然塑形眉笔</div>
				<div class="subTil">
					<div class="price">¥48<span>¥85</span></div>
					<div>赠送积分：45</div>
					<div>已售500</div>
				</div>
			</div>
				
			<!--优惠-->
			<div class="disView div_float" @click="showDisFun">
				<div class="L">
					<label>优惠</label>
					<span>满100减20</span>
					<span>满100减20</span>
				</div>
				<div class="R">共10个 ></div>
			</div>
			
			<!--已选-->
			<div class="selView">
				<div class="head">
					<label>已选</label>
					<span>50ml</span>
					<span>5件</span>
				</div>
				<div class="selCn">
					<div class="selList">
						<label>规格</label>
						<span>50ml</span>
						<span>50ml</span>
					</div>
				</div>
				<div class="selNum">
						<label>数量</label>
						<div class="num">
							<span @click='changeToatl' data-c-type="cut" :data-c-index="key">-</span>
							<input type="number" value="1" disabled="disabled" />
							<span @click='changeToatl' data-c-type="add" :data-c-index="key">+</span>
						</div>
				</div>
			</div>
			
			<!--产品参数-->
			<div class="ptView">
				<div class="list">
					<div class="top div_float" @click="showProFun">
						<div>全部商品</div>
						<div><span class="arr_g down" :class="[shouPro ?'up':'']"></span></div>
					</div>
					<div class="btm" v-show="shouPro">
						<div>
							<div>品名：</div>
							<div>粉黛自然塑眉笔</div>
						</div>
						<div>
							<div>品名：</div>
							<div>粉黛自然塑眉笔</div>
						</div>
					</div>
				</div>
			</div>
			
			<!--商品详情-->
			<div class="detailView">
				<div class="til">商品介绍</div>
				<div class="deCn"></div>
			</div>
			
			<!--底部-->
			<div class="proBtm div_float">
				<div class="L">
					<div>
						<div class="seIcon"></div>
						<div>客服</div>
					</div>
					<div>
						<div class="cartIcon"></div>
						<div>购物车</div>
					</div>
					<div>
						<div class="homeIcon"></div>
						<div>首页</div>
					</div>
				</div>
				<div class="R">
					<div>加入购物车</div>
					<div>立即购买</div>
				</div>
			</div>
			
			<!--优惠券-->
			<div class="discount" v-show="showDis">
				<div class="sub">
					<div class="disCnView">
						<div class="close"><span class="closeIcon" @click="showDisFun"></span></div>
						<div class="til">优惠券</div>
						 <div class="disCn div_float">
						 	<div v-for="(item,key) in disList" :key='item.id'>
						 		<div class="cnSub div_float">
						 			<div class="L">
							 			<div class="lSub">
							 				<div class="til">赠品</div>
							 				<div>满198可用</div>
							 			</div>
							 		</div>
							 		<div class="R"><span>领取</span></div>
						 		</div>
						 	</div>
						 </div>
					</div>
				</div>
			</div>
    
  </div>
</template>

<script>

export default {
  data () {
    return {
    	bannerList:[1,1,1],
    	bSel:0,
    	disList:[1,1,1,1,1,1],
    	showDis:false,//是否显示优惠券
    	shouPro:false,//是否显示商品参数
    }
  },

  components: {
    
  },

  methods: {
  	//轮播图切换
    bannerChange(e){
    	this.bSel = parseInt(e.mp.detail.current);
    },
    //点击查看参数
    showProFun(){
    	var sign = true;
    	if(this.shouPro){
    		sign = false;
    	}
    	this.shouPro = sign;
    },
    //点击查看优惠券
    showDisFun(){
    	var sign = true;
    	if(this.showDis){
    		sign = false;
    	}
    	this.showDis = sign;
    }
  },

  created () {
   
  }
}
</script>

<style scoped>
	.discount .disCn{
		overflow-y: auto;
	}
	.discount .disCn>div{
		float: left;
		width: 50%;
		margin: 12rpx 0;
	}
	.discount .disCn .cnSub{
		width: 90%;
		height: 116rpx;
		line-height: 116rpx;
		margin: 0 auto;
		border-radius: 28rpx;
		border: solid 2rpx #5b0e12;
	}
	.cnSub .L{
		float: left;
		width: 40%;
		line-height: 116rpx;
		text-align: center;
		color: #666666;
		font-size: 24rpx;
		line-height: 50rpx;
		margin-left: 21rpx;
	}
	.cnSub .L .lSub{
		line-height: 40rpx;
		display: inline-block;
		vertical-align: middle;
	}
	.cnSub .L .til{
		color: #333333;
		font-size: 32rpx;
	}
	.cnSub .R{
		float: right;
		margin-right: 21rpx;
	}
	.cnSub .R span{
		display: inline-block;
		vertical-align: middle;
		width: 120rpx;
		height: 64rpx;
		background-color: #5b0e12;
		border-radius: 8rpx;
		line-height: 64rpx;
		color: white;
		font-size: 24rpx;
		text-align: center;
	}
	.discount .til{
		height: 60rpx;
		line-height: 60rpx;
		padding: 0 2%;
		color: #333333;
		font-size: 28rpx;
	}
	.discount{
		position: fixed;
		width: 100%;
		top: 0;
		bottom: 0;
		z-index: 4;
		background-color: rgba(0,0,0,.4);
	}
	.discount .sub{
		position: inherit;
		height: 600rpx;
		width: 100%;
		bottom: 0;
		overflow-y: auto;
		background-color: white;
	}
	.discount .disCnView{
		position: relative;
	}
	.discount .close{
		height: 80rpx;
		line-height: 80rpx;
		text-align: right;
		padding: 0 2%;
	}
	.discount .close span{
		display: inline-block;
		vertical-align: middle;
		width: 60rpx;
		height: 60rpx;
	}
	.proBtm{
		height: 110rpx;
		line-height: 110rpx;
		background-color: white;
		position: fixed;
		width: 96%;
		bottom: 0;
		padding:  0 2%;
		z-index: 3;
	}
	.proBtm .L{
		float: left;
		width: 40%;
		font-size: 0;
	}
	.proBtm .L>div{
		display: inline-block;
		vertical-align: middle;
		color: #666666;
		text-align: center;
		font-size: 20rpx;
		width: 33.33%;
		line-height: 40rpx;
	}
	.proBtm .L>div>div:nth-child(1){
		display: inline-block;
		vertical-align: middle;
		width: 54rpx;
		height: 54rpx;
		margin-top:10rpx;
	}
	.proBtm .L>div>div:nth-child(2){
		font-size: 20rpx;
		color: #666666;
	}
	.proBtm .R{
		float: right;
		width: 60%;
		font-size: 0;
	}
	.proBtm .R>div{
		display: inline-block;
		vertical-align: middle;
		width: 180rpx;
		height: 80rpx;
		border-radius: 8rpx;
		border: solid 2rpx #5b0e12;
		text-align: center;
		line-height: 80rpx;
		font-size: 28rpx;
	}
	.proBtm .R>div:nth-child(1){
		color: #5B0E12;
		margin-right: 24rpx;
		margin-left: 30rpx;
	}
	.proBtm .R>div:nth-child(2){
		color: white;
		background-color: #5B0E12;
	}
	.detailView{
		margin-top: 20rpx;
		background-color: white;
	}
	.detailView .til{
		height: 88rpx;
		line-height: 88rpx;
		text-align: center;
		color: #333333;
		font-size: 32rpx;
	}
	.ptView .btm{
		padding-bottom: 20rpx;
	}
	.ptView .btm>div{
		height: 80rpx;
		line-height: 80rpx;
		padding: 0 2%;
		border-bottom: 2rpx solid #E5E5E5;
		color: #666666;
		font-size: 24rpx;
	}
	.ptView .btm>div>div:nth-child(1){
		float: left;
		width: 20%;
	}
	.ptView .btm>div>div:nth-child(2){
		float: left;
	}
	.ptView .list .top{
		height: 80rpx;
		line-height: 80rpx;
		padding: 0 2%;
		background-color: white;
		margin-top: 20rpx;
		color: #666666;
		font-size: 26rpx;
		border-bottom: 2rpx solid #E5E5E5;
	}
	.ptView .list .top>div:nth-child(1){
		float: left;
	}
	.ptView .list .top>div:nth-child(2){
		float: right;
	}
	.ptView .list .top>div:nth-child(2) span{
		display: inline-block;
		vertical-align: middle;
		width: 16rpx;
		height: 28rpx;
		margin-top: -5rpx;
	}
	.ptView .list .top .down{
		transform: rotate(90deg);
		margin-right: 9rpx;
	}
	.ptView .list .top .up{
		transform: rotate(270deg);
		margin-right: 9rpx;
	}
	.ptView{
		background-color: white;
		margin-top: 20rpx;
	}
	.selNum{
		height: 80rpx;
		line-height: 80rpx;
		padding: 0 2%;
	}
	.selNum .num {
		right: 2%;
		bottom: 24rpx;
		line-height: 40rpx;
		color: #333333;
		font-size: 24rpx;
		font-size: 0;
		display: inline-block;
		vertical-align: middle;
		margin-left: 20rpx;
	}
	
	.selNum .num span,
	.selNum .num input {
		display: inline-block;
		vertical-align: middle;
		text-align: center;
	}
	
	.selNum .num span {
		width: 48rpx !important;
		height: 48rpx !important;
		font-size: 24rpx;
		color: #666666;
		border: 2rpx solid #E5E5E5;
	}
	
	.selNum .num input {
		width: 80rpx;
		height: 48rpx;
		font-size: 26rpx;
		color: #666666;
		border-top: 2rpx solid #E5E5E5;
		border-bottom: 2rpx solid #E5E5E5;
		min-height: 48rpx;
		line-height: 48rpx !important;
	}
	.selList{
		height: 60rpx;
		line-height: 60rpx;
		padding: 0 2%;
	}
	.selList span{
		display: inline-block;
		vertical-align: middle;
		padding: 0 33rpx;
		border: 2rpx solid #E5E5E5;
		color: #666666;
		text-align: center;
		height: 44rpx;
		line-height: 44rpx;
		margin:10rpx;
		border-radius: 8rpx;
		font-size: 20rpx;
	}
	.selView{
		background-color: white;
		margin-top: 20rpx;
	}
	.selView .head{
		height: 83rpx;
		line-height: 83rpx;
		padding: 0 2%;
		border-bottom: 2rpx solid #E5E5E5;
	}
	.selView .head span{
		display: inline-block;
		vertical-align: middle;
		color: #666666;
		font-size: 20rpx;
		margin: 0 20rpx;
	}
	.disView{
		height: 72rpx;
		line-height: 72rpx;
		padding: 0 2%;
		background-color: white;
		margin-top: 20rpx;
	}
	.disView .R{
		float: right;
		color: #333333;
		font-size: 24rpx;
	}
	.disView .L{
		float: left;
	}
	.disView .L label,.selView .head label,.selList label,.selNum label{
		color: #333333;
		font-size: 24rpx;
	}
	.disView .L span{
		border-radius: 8rpx;
		border: solid 2rpx #5b0e12;
		height: 32rpx;
		line-height: 32rpx;
		color: #5B0E12;
		padding: 0 22rpx;
		display: inline-block;
		vertical-align: middle;
		margin: 0 10rpx;
		font-size: 20rpx;
	}
	.proTil .subTil>div{
		display: inline-block;
		vertical-align: middle;
		width: 33.33%;
		color: #999999;
		font-size: 20rpx;
	}
	.proTil .subTil>div:nth-child(2){
		text-align: center;
	}
	.proTil .subTil>div:nth-child(3){
		text-align: right;
	}
	.proTil .subTil .price{
		color: #5b0e12;
		font-size: 24rpx;
	}
	.proTil .subTil span{
		color: #999999;
		font-size: 20rpx;
		text-decoration: line-through;
		margin-left: 20rpx;
	}
	.proTil .subTil{
		height: 80rpx;
		line-height: 80rpx;
	}
	.proTil .til{
		line-height: 50rpx;
		color: #333333;
		font-size: 28rpx;
		margin-top: 20rpx;
	}
	.proTil{
		background-color: white;
		padding: 0 2%;
	}
	.bannerView{
		position: relative;
	}
	.banner{
		height: 750rpx;
	}
	.banner swiper-item img{
		width: 100%;
		height: 100%;
	}
	.pageTion{
		position: absolute;
		bottom: 20rpx;
		width: 100%;
		text-align: center;
	}
	.pageTion span{
		display: inline-block;
		vertical-align: middle;
		width: 24rpx;
		height: 24rpx;
		border-radius: 100%;
		border: solid 2rpx #666666;
		margin: 0 10rpx;
	}
	.pageTion  .sel{
		background-color: #666666;
	}
	.container{
		background-color: #F7F7F7;
		padding-bottom: 150rpx;
	}
	

	
	.seIcon{
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	.cartIcon{
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	.homeIcon{
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	
	.closeIcon{
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
</style>



